.App {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-family: Gotham, roboto, sans-serif, avenir, arial, sans-serif;
}

.Nav {
  padding: 0px 10%;
  box-sizing: border-box;
  width: 100%;
  height: 12%;
  background-color: rgb(246, 246, 246);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.Nav-title {
  font-size: 28px;
  font-weight: bold;
  font-family: 'FreightBigPro';
}

.Nav-image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90px;
  height: 40px;
  object-fit: contain;
}

.Nav-cat {
  padding: 0px 10%;
  width: 100%;
  height: 8%;
  box-sizing: border-box;
  background-color: rgb(246, 246, 246);
}

.Nav-cat-item {
  position: absolute;
  left: 8%;
  top: 6%;
  width: calc(84%);
  height: calc(94%);
  color: rgb(150, 150, 150);
  font-size: 12px;
  line-height: 1;
}

.Select {
  appearance: none;
  background-image: url('data:image/png;base64,');
  left: 0px;
  top: 0px;
  padding: 0px 14px 0px 0px;
  background-size: 20px;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: calc(100% - 1em) center;
  background-color: white;
  color: black;
  font-family: Gotham;
  border: 1px solid rgb(239, 239, 239);
}

option:checked {
  color: rgb(97, 75, 121);
  font-weight: bold;
}

.Select:focus-visible {
  outline: none;
}

a {
  cursor: pointer;
}